<template>
  <div class="content drawManage">
<div class="operation">
    <el-form :inline="true"  label-width="100px">
      <el-form-item label="申请提现状态">
        <el-select v-model="tempData.applyStatus">
        <el-option v-for="item in statusList" :key="item.value" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="起始时间">
        <el-date-picker type="date" value-format="yyyy-mm-dd" v-model="tempData.startTime"></el-date-picker>
      </el-form-item>
      <el-form-item label="截止时间">
        <el-date-picker type="date" value-format="yyyy-mm-dd" v-model="tempData.endTime"></el-date-picker>
      </el-form-item>
      <el-form-item label="手机号码" class="mgb0">
        <el-input placeholder="请输入配送员手机号" v-model="tempData.phone"></el-input>
      </el-form-item>
      <el-form-item label="真实姓名"  class="mgb0">
        <el-input placeholder="请输入配送员真实姓名" v-model="tempData.realname"></el-input>
      </el-form-item>
      <el-form-item class="mgb0">
        <el-button type="primary" @click="searchList">查询</el-button>
      </el-form-item>
    </el-form>
  </div>
  <div class="table">
    <el-table v-loading="loading" border :data="tableData">
      <el-table-column label="手机号码" prop="phone"></el-table-column>
      <el-table-column label="真实姓名" prop="realname"></el-table-column>
      <el-table-column label="配送员ID" prop="disId"></el-table-column>
      <el-table-column label="申请提现状态" prop="applyStatus"></el-table-column>
      <el-table-column label="提现时间" prop="createTime"></el-table-column>
      <el-table-column label="开户银行" prop="bankName"></el-table-column>
      <el-table-column prop="cardNumber" label="银行卡号"></el-table-column>
      <el-table-column prop="commissionChange" label="提现金额"></el-table-column>
    </el-table>
  </div>
    <pagination
      :current-page="page.pageCurrent"
      :page-size="page.pageSize"
      :total="page.pageNum"
      @sizeChange="sizeChange"
      @currentChange="currentChange"
    ></pagination>
  </div>
</template>

<script>
 import pagination from "@/components/pagination"
 import {withDraw} from "@/api/index.js"
export default {
     components: {
        pagination
     },
     data(){
         return{
           statusList:[
             {label:'待处理',value:1},{label:'提现成功',value:2},{label:'提现失败',value:3}
           ],
       //加载状态
      loading: false,
      //表格数据
      tableData: [],
      //搜索时临时数据
      tempData: {
      },
      //搜索时用的数据
      searchData: {},
      //分页数据
      page: {
        pageNum: null,
        pageCurrent: 1,
        pageSize: 5
      },
         }
     },
     methods:{
    initData: function(page,size) {
      this.loading = true;
      this.searchData=this.$common.deepCopy(this.tempData)
      this.searchData.page=this.page.pageCurrent
      this.searchData.size=this.page.pageSize
      withDraw.deliverWithDraw(this.searchData
      ).then(res => {
        this.tableData = res.rows;
        this.page.pageNum = res.count;
      });
      setTimeout(() => {
        this.loading = false;
      }, 500);
    },
         //条件查询
   searchList: function () {
        this.searchData=this.$common.deepCopy(this.tempData)
        this.page.pageCurrent = 1;
        this.initData(1)
   },
    //页数改变时的回调
    currentChange(val) {
      this.page.pageCurrent = val;
      this.initData();
    },
    //每页总数改变时的回调
    sizeChange(val) {
      this.page.pageCurrent = 1;
      this.initData(1);
    },
     },
     beforeMount() {
    this.initData();
  }

}
</script>

<style>

</style>



